.approach {
  width: 90%;
  height: 900px;
  background: #ff9c8e;
  border-radius: 200px 0 0 200px;
  position: relative;
  left: 10%;
  top: -100px;

  &__image {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 45%;
  }

  &__content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10% 15%;
    color: #fff;
    box-sizing: border-box;
  }

  &__label {
    @extend %label;
    color: #fff;
  }

  &__name {
    font-weight: 600;
    font-size: 32px;
  }

  &__round {
    background-color: #fff;
    width: 64px;
    height: 64px;
    display: inline-block;
    text-align: center;
    line-height: 70px;
    border-radius: 50%;
    margin-right: 5%;
    transition: all 0.5s ease-in-out;
    &:hover {
      transform: scale(1.3);
    }
  }
  &__description {
    display: inline-block;
  }

  &__block {
    display: flex;
    align-items: center;
  }
}
